/**
 * Synchrophaser system for twin propellers using Phase-Locked Loop (PLL) control.
 *
 * Implements PID controller to minimize phase error between main and follower propellers.
 */

import {
  SYNCHRO_K_P,
  SYNCHRO_K_I,
  SYNCHRO_K_D,
  SYNCHRO_INTEGRATOR_MIN,
  SYNCHRO_INTEGRATOR_MAX,
  SYNCHRO_PHASE_SCALE,
  SYNCHRO_DERIVATIVE_FILTER_ALPHA,
  SYNCHRO_DEADBAND,
  SYNCHRO_RATE_LIMIT
} from './parameters';

export class Synchrophaser {
  /**
   * Phase-Locked Loop (PLL) based synchrophaser for twin propellers.
   *
   * Control Strategy:
   *   1. Measure phase error: _error = _main - _follower
   *   2. PID controller: correction = K_P * _error + K_I * _error dt + K_D * d_error/dt
   *   3. Adjust follower RPM setpoint: RPM_follower = RPM_nominal + correction
   */
  constructor({
    kP = SYNCHRO_K_P,
    kI = SYNCHRO_K_I,
    kD = SYNCHRO_K_D,
    integratorMin = SYNCHRO_INTEGRATOR_MIN,
    integratorMax = SYNCHRO_INTEGRATOR_MAX,
    phaseScale = SYNCHRO_PHASE_SCALE,
    derivativeFilterAlpha = SYNCHRO_DERIVATIVE_FILTER_ALPHA,
    deadband = SYNCHRO_DEADBAND,
    rateLimit = SYNCHRO_RATE_LIMIT
  } = {}) {
    // Control gains (PID)
    this.kP = kP;
    this.kI = kI;
    this.kD = kD;
    this.phaseScale = phaseScale;

    // Anti-overshoot parameters
    this.derivativeFilterAlpha = derivativeFilterAlpha;
    this.deadband = deadband;
    this.rateLimit = rateLimit;

    // Integrator state and limits (anti-windup)
    this.integrator = 0.0;
    this.integratorMin = integratorMin;
    this.integratorMax = integratorMax;

    // State tracking
    this.enabled = false;
    this.phaseError = 0.0;  // radians
    this.previousPhaseError = 0.0;
    this.filteredDerivative = 0.0;
    this.previousRpmCorrection = 0.0;
    this.rpmCorrection = 0.0;
    this.proportionalTerm = 0.0;
    this.integralTerm = 0.0;
    this.derivativeTerm = 0.0;

    // Statistics
    this.maxPhaseError = 0.0;
    this.cumulativeError = 0.0;
    this.updateCount = 0;
  }

  /**
   * Enable synchrophaser (activate phase locking).
   */
  enable() {
    this.enabled = true;
    // Reset all state to prevent startup transients
    this.integrator = 0.0;
    this.phaseError = 0.0;
    this.previousPhaseError = 0.0;
    this.filteredDerivative = 0.0;
    this.previousRpmCorrection = 0.0;
  }

  /**
   * Disable synchrophaser (propellers run independently).
   */
  disable() {
    this.enabled = false;
    // Reset all state when disabled
    this.integrator = 0.0;
    this.phaseError = 0.0;
    this.previousPhaseError = 0.0;
    this.filteredDerivative = 0.0;
    this.previousRpmCorrection = 0.0;
  }

  /**
   * Reset statistics counters.
   */
  resetStats() {
    this.maxPhaseError = 0.0;
    this.cumulativeError = 0.0;
    this.updateCount = 0;
    this.integrator = 0.0;
  }

  /**
   * Compute TRUE phase error from actual blade positions.
   *
   * Returns phase error in radians, wrapped to [-, ]
   * Positive error means main is ahead, follower needs to speed up
   */
  computePhaseError(bladeAngleMain, bladeAngleFollower) {
    // Compute raw phase difference
    const rawError = bladeAngleMain - bladeAngleFollower;

    // Wrap to [-, ] for cyclic phase
    const phaseError = Math.atan2(Math.sin(rawError), Math.cos(rawError));

    return phaseError;
  }

  /**
   * Update synchrophaser and compute RPM correction for follower.
   *
   * Uses TRUE PHASE ERROR from blade positions with PID control
   * PLUS anti-overshoot features: derivative filtering, deadband, rate limiting.
   */
  update(bladeAngleMain, bladeAngleFollower, dt) {
    if (!this.enabled) {
      this.phaseError = 0.0;
      this.rpmCorrection = 0.0;
      this.proportionalTerm = 0.0;
      this.integralTerm = 0.0;
      this.derivativeTerm = 0.0;
      return 0.0;
    }

    // Compute TRUE phase error from actual blade positions
    const rawPhaseError = this.computePhaseError(bladeAngleMain, bladeAngleFollower);

    // DEADBAND: Ignore very small errors to reduce jitter
    if (Math.abs(rawPhaseError) < this.deadband) {
      this.phaseError = 0.0;
      // Slowly decay integrator toward zero when in deadband
      this.integrator *= 0.99;
    } else {
      this.phaseError = rawPhaseError;
    }

    // Update statistics (use raw error, not deadband-filtered)
    this.updateCount += 1;
    this.cumulativeError += Math.abs(rawPhaseError);
    this.maxPhaseError = Math.max(this.maxPhaseError, Math.abs(rawPhaseError));

    // Proportional term (reacts to current phase error)
    this.proportionalTerm = this.kP * this.phaseError;

    // Integral term (eliminates steady-state error)
    if (Math.abs(this.phaseError) > 0) {
      this.integrator += this.phaseError * dt;
      // Anti-windup: clamp integrator tightly
      const maxIntegral = 0.5;  // Tight limit (radians * seconds)
      this.integrator = Math.max(-maxIntegral, Math.min(maxIntegral, this.integrator));
    }
    this.integralTerm = this.kI * this.integrator;

    // Derivative term with LOW-PASS FILTER to reduce noise amplification
    if (dt > 0) {
      // Compute raw derivative
      const rawDerivative = (this.phaseError - this.previousPhaseError) / dt;

      // Apply exponential moving average filter
      this.filteredDerivative = (
        this.derivativeFilterAlpha * rawDerivative +
        (1.0 - this.derivativeFilterAlpha) * this.filteredDerivative
      );

      // Use filtered derivative
      this.derivativeTerm = this.kD * this.filteredDerivative;
    } else {
      this.derivativeTerm = 0.0;
    }

    // Store for next iteration
    this.previousPhaseError = this.phaseError;

    // Full PID correction: convert phase error (radians) to RPM correction
    const phaseCorrectionRadians = this.proportionalTerm + this.integralTerm + this.derivativeTerm;
    const desiredRpmCorrection = phaseCorrectionRadians * this.phaseScale;

    // RATE LIMITING: Prevent sudden jumps in correction
    if (dt > 0) {
      const maxChange = this.rateLimit * dt;  // Max change per timestep
      let correctionDelta = desiredRpmCorrection - this.previousRpmCorrection;
      correctionDelta = Math.max(-maxChange, Math.min(maxChange, correctionDelta));
      this.rpmCorrection = this.previousRpmCorrection + correctionDelta;
    } else {
      this.rpmCorrection = desiredRpmCorrection;
    }

    // Final safety limit
    this.rpmCorrection = Math.max(-15.0, Math.min(15.0, this.rpmCorrection));

    // Store for next iteration
    this.previousRpmCorrection = this.rpmCorrection;

    return this.rpmCorrection;
  }

  /**
   * Get synchrophaser statistics.
   */
  getStats() {
    const meanError = this.updateCount === 0 ? 0.0 : this.cumulativeError / this.updateCount;

    return {
      enabled: this.enabled,
      phaseError: this.phaseError,
      phaseErrorDeg: this.phaseError * 180 / Math.PI,
      rpmCorrection: this.rpmCorrection,
      proportionalTerm: this.proportionalTerm,
      integralTerm: this.integralTerm,
      integrator: this.integrator,
      maxPhaseError: this.maxPhaseError,
      maxPhaseErrorDeg: this.maxPhaseError * 180 / Math.PI,
      meanAbsError: meanError,
      meanAbsErrorDeg: meanError * 180 / Math.PI,
      updateCount: this.updateCount
    };
  }
}
