/**
 * Atmospheric density field using Simplex noise with fractal Brownian motion.
 *
 * Generates smooth, time-varying density variations representing atmospheric turbulence.
 * The field drifts horizontally to simulate apparent motion through turbulent air.
 */

import { createNoise2D } from 'simplex-noise';
import {
  WAVELENGTH_DEFAULT,
  RHO_MIN_DEFAULT,
  RHO_MAX_DEFAULT,
  DRIFT_VELOCITY_DEFAULT,
  NUM_OCTAVES_DEFAULT,
  PERSISTENCE,
  LACUNARITY,
  DEFAULT_SEED
} from './parameters';

// Simple seeded random number generator for reproducibility
function createSeededRandom(seed) {
  return function() {
    seed = (seed * 9301 + 49297) % 233280;
    return seed / 233280;
  };
}

export class DensityField {
  /**
   * Atmospheric density field using Simplex noise.
   *
   * Generates smooth, time-varying density variations based on fractal
   * Brownian motion (fBm). The field drifts horizontally at a specified
   * velocity to simulate motion through turbulent air.
   */
  constructor({
    wavelength = WAVELENGTH_DEFAULT,
    rhoMin = RHO_MIN_DEFAULT,
    rhoMax = RHO_MAX_DEFAULT,
    driftVelocity = DRIFT_VELOCITY_DEFAULT,
    numOctaves = NUM_OCTAVES_DEFAULT,
    seed = DEFAULT_SEED
  } = {}) {
    this.wavelength = wavelength;
    this.rhoMin = rhoMin;
    this.rhoMax = rhoMax;
    this.driftVelocity = driftVelocity;
    this.numOctaves = numOctaves;
    this.seed = seed;

    // Initialize Simplex noise generator with seeded random
    const seededRandom = createSeededRandom(seed);
    this.noise = createNoise2D(seededRandom);

    // Calculate base frequency from wavelength
    this.baseFrequency = 1.0 / wavelength;

    // Pre-calculate normalization factor for fBm
    this.amplitudeSum = 0;
    for (let i = 0; i < numOctaves; i++) {
      this.amplitudeSum += Math.pow(PERSISTENCE, i);
    }
  }

  /**
   * Generate 2D fractal Brownian motion noise at position (x, y).
   *
   * Combines multiple octaves of noise with decreasing amplitude and
   * increasing frequency to create natural-looking turbulence.
   */
  _fbmNoise2D(x, y) {
    let value = 0.0;
    let amplitude = 1.0;
    let frequency = this.baseFrequency;

    for (let octave = 0; octave < this.numOctaves; octave++) {
      // Sample noise at current frequency
      const sampleX = x * frequency;
      const sampleY = y * frequency;

      // Simplex noise returns values in [-1, 1]
      const noiseValue = this.noise(sampleX, sampleY);

      // Accumulate weighted noise
      value += noiseValue * amplitude;

      // Update for next octave
      amplitude *= PERSISTENCE;
      frequency *= LACUNARITY;
    }

    // Normalize to [0, 1] range
    const normalized = (value / this.amplitudeSum + 1.0) / 2.0;

    // Clamp to ensure we stay in [0, 1]
    return Math.max(0.0, Math.min(1.0, normalized));
  }

  /**
   * Sample atmospheric density at position (x, y) at given time.
   *
   * This is the key interface that propellers use.
   * The density field drifts horizontally at driftVelocity, simulating
   * motion through a frozen turbulence pattern (Taylor's hypothesis).
   */
  getDensity(x, y, time) {
    // Apply drift: shift the sampling position backward in time
    const xDrifted = x - this.driftVelocity * time;

    // Sample the noise field
    const noiseValue = this._fbmNoise2D(xDrifted, y);

    // Map [0, 1] noise to [rhoMin, rhoMax] density
    const density = this.rhoMin + noiseValue * (this.rhoMax - this.rhoMin);

    return density;
  }

  /**
   * Generate a 2D grid of density values for visualization.
   *
   * Returns a 2D array of densities with shape [yArray.length, xArray.length]
   */
  getDensityGrid(xArray, yArray, time) {
    const grid = [];

    for (let i = 0; i < yArray.length; i++) {
      const row = [];
      for (let j = 0; j < xArray.length; j++) {
        const x = xArray[j];
        const y = yArray[i];
        const density = this.getDensity(x, y, time);
        row.push(density);
      }
      grid.push(row);
    }

    return grid;
  }

  /**
   * Calculate statistical properties of a density grid.
   */
  getStatistics(grid) {
    let sum = 0;
    let min = Infinity;
    let max = -Infinity;
    let count = 0;

    for (const row of grid) {
      for (const value of row) {
        sum += value;
        min = Math.min(min, value);
        max = Math.max(max, value);
        count++;
      }
    }

    const mean = sum / count;

    // Calculate standard deviation
    let sumSquaredDiff = 0;
    for (const row of grid) {
      for (const value of row) {
        sumSquaredDiff += Math.pow(value - mean, 2);
      }
    }
    const std = Math.sqrt(sumSquaredDiff / count);

    return { mean, min, max, std };
  }
}
