/**
 * Phase-Frequency Detector (PFD) based synchrophaser.
 *
 * Modern PLL architecture using both phase and frequency error for robust
 * synchronization. This is a LEGITIMATE improvement over simple phase detection.
 */

import { Synchrophaser } from './synchrophaser';
import {
  SYNCHRO_K_P,
  SYNCHRO_K_I,
  SYNCHRO_K_D,
  SYNCHRO_INTEGRATOR_MIN,
  SYNCHRO_INTEGRATOR_MAX,
  SYNCHRO_PHASE_SCALE,
  SYNCHRO_DERIVATIVE_FILTER_ALPHA,
  SYNCHRO_DEADBAND,
  SYNCHRO_RATE_LIMIT
} from './parameters';

export class PFDSynchrophaser extends Synchrophaser {
  /**
   * Phase-Frequency Detector based synchrophaser.
   *
   * Key difference from baseline: Uses BOTH phase and frequency errors.
   *
   * Traditional Phase Detector:
   *   error = phase_main - phase_follower
   *   correction = PID(phase_error)
   *
   * Phase-Frequency Detector:
   *   phase_error = integrated cumulative phase difference
   *   freq_error = (_main - _follower)
   *   correction = PID(phase_error) + K_F * freq_error
   */
  constructor({
    kP = SYNCHRO_K_P,
    kI = SYNCHRO_K_I,
    kD = SYNCHRO_K_D,
    kF = 0.5,  // NEW: Frequency error gain
    integratorMin = SYNCHRO_INTEGRATOR_MIN,
    integratorMax = SYNCHRO_INTEGRATOR_MAX,
    phaseScale = SYNCHRO_PHASE_SCALE,
    derivativeFilterAlpha = SYNCHRO_DERIVATIVE_FILTER_ALPHA,
    deadband = SYNCHRO_DEADBAND,
    rateLimit = SYNCHRO_RATE_LIMIT
  } = {}) {
    // Initialize baseline synchrophaser
    super({
      kP,
      kI,
      kD,
      integratorMin,
      integratorMax,
      phaseScale,
      derivativeFilterAlpha,
      deadband,
      rateLimit
    });

    // NEW: Frequency error gain
    this.kF = kF;

    // Frequency error tracking
    this.freqError = 0.0;  // rad/s (angular velocity difference)
    this.freqErrorRpm = 0.0;  // RPM (for display)
    this.frequencyTerm = 0.0;  // Contribution from frequency error

    // Filtered frequency error (smooth out noise)
    this.filteredFreqError = 0.0;
    this.freqFilterAlpha = 0.1;  // Low-pass filter for frequency
  }

  /**
   * Enable synchrophaser and reset all state.
   */
  enable() {
    super.enable();

    // Reset frequency error state
    this.freqError = 0.0;
    this.freqErrorRpm = 0.0;
    this.frequencyTerm = 0.0;
    this.filteredFreqError = 0.0;
  }

  /**
   * Disable synchrophaser and reset all state.
   */
  disable() {
    super.disable();

    // Reset frequency error state
    this.freqError = 0.0;
    this.freqErrorRpm = 0.0;
    this.frequencyTerm = 0.0;
    this.filteredFreqError = 0.0;
  }

  /**
   * Update PFD synchrophaser with phase AND frequency errors.
   *
   * This is the key difference: we use omega (frequency) in addition
   * to blade angles (phase).
   */
  update(bladeAngleMain, bladeAngleFollower, omegaMain, omegaFollower, dt) {
    if (!this.enabled) {
      this.phaseError = 0.0;
      this.freqError = 0.0;
      this.rpmCorrection = 0.0;
      this.proportionalTerm = 0.0;
      this.integralTerm = 0.0;
      this.derivativeTerm = 0.0;
      this.frequencyTerm = 0.0;
      return 0.0;
    }

    // ================================================================
    // PHASE-FREQUENCY DETECTION - This is the advanced feature!
    // ================================================================

    // 1. Phase error (same as baseline)
    const rawPhaseError = this.computePhaseError(
      bladeAngleMain,
      bladeAngleFollower
    );

    // 2. Frequency error (NEW - this is the PFD improvement!)
    this.freqError = omegaMain - omegaFollower;  // rad/s

    // Filter frequency error to reduce noise
    this.filteredFreqError = (
      this.freqFilterAlpha * this.freqError +
      (1.0 - this.freqFilterAlpha) * this.filteredFreqError
    );

    // Convert to RPM for display
    this.freqErrorRpm = this.freqError * 60.0 / (2.0 * Math.PI);

    // ================================================================
    // Control using BOTH phase and frequency
    // ================================================================

    // Apply deadband to phase error
    if (Math.abs(rawPhaseError) < this.deadband) {
      this.phaseError = 0.0;
      this.integrator *= 0.99;  // Decay integrator in deadband
    } else {
      this.phaseError = rawPhaseError;
    }

    // Update statistics
    this.updateCount += 1;
    this.cumulativeError += Math.abs(rawPhaseError);
    this.maxPhaseError = Math.max(this.maxPhaseError, Math.abs(rawPhaseError));

    // Proportional term (phase error)
    this.proportionalTerm = this.kP * this.phaseError;

    // Integral term (phase error accumulation)
    if (Math.abs(this.phaseError) > 0) {
      this.integrator += this.phaseError * dt;
      const maxIntegral = 0.5;
      this.integrator = Math.max(-maxIntegral, Math.min(maxIntegral, this.integrator));
    }
    this.integralTerm = this.kI * this.integrator;

    // Derivative term (phase error rate of change)
    if (dt > 0) {
      const rawDerivative = (this.phaseError - this.previousPhaseError) / dt;
      this.filteredDerivative = (
        this.derivativeFilterAlpha * rawDerivative +
        (1.0 - this.derivativeFilterAlpha) * this.filteredDerivative
      );
      this.derivativeTerm = this.kD * this.filteredDerivative;
    } else {
      this.derivativeTerm = 0.0;
    }

    // Store for next iteration
    this.previousPhaseError = this.phaseError;

    // NEW: Frequency term (this is the PFD improvement!)
    // Frequency error provides "velocity" feedback
    // Helps pull frequencies together before fine-tuning phase
    this.frequencyTerm = this.kF * this.filteredFreqError;

    // Total correction: PID on phase + proportional on frequency
    const phaseCorrectionRadians = (
      this.proportionalTerm +
      this.integralTerm +
      this.derivativeTerm
    );
    const phaseCorrectionRpm = phaseCorrectionRadians * this.phaseScale;

    // Add frequency term (directly in RPM)
    const freqCorrectionRpm = this.frequencyTerm * 60.0 / (2.0 * Math.PI);

    // Combined correction
    const desiredRpmCorrection = phaseCorrectionRpm + freqCorrectionRpm;

    // RATE LIMITING: Prevent sudden jumps
    if (dt > 0) {
      const maxChange = this.rateLimit * dt;
      let correctionDelta = desiredRpmCorrection - this.previousRpmCorrection;
      correctionDelta = Math.max(-maxChange, Math.min(maxChange, correctionDelta));
      this.rpmCorrection = this.previousRpmCorrection + correctionDelta;
    } else {
      this.rpmCorrection = desiredRpmCorrection;
    }

    // Final safety limit
    this.rpmCorrection = Math.max(-15.0, Math.min(15.0, this.rpmCorrection));

    // Store for next iteration
    this.previousRpmCorrection = this.rpmCorrection;

    return this.rpmCorrection;
  }

  /**
   * Get synchrophaser statistics including frequency error.
   */
  getStats() {
    // Get baseline stats
    const stats = super.getStats();

    // Add PFD-specific statistics
    stats.freqError = this.freqError;
    stats.freqErrorRpm = this.freqErrorRpm;
    stats.frequencyTerm = this.frequencyTerm;
    stats.filteredFreqError = this.filteredFreqError;

    return stats;
  }
}
