/**
 * Propeller dynamics with simple governor control.
 *
 * The propeller attempts to maintain constant RPM but varies with local air density.
 */

import {
  PROPELLER_X_DEFAULT,
  PROPELLER_Y_DEFAULT,
  PROPELLER_INERTIA,
  PROPELLER_RPM_NOMINAL,
  K_AERO,
  GOVERNOR_K_P,
  GOVERNOR_BASE_TORQUE,
  RHO_SEA_LEVEL
} from './parameters';

export class Propeller {
  /**
   * Single propeller with aerodynamic loading and governor control.
   *
   * Physics:
   *   I * d/dt = Q_engine - Q_aero
   *   Q_aero = k_aero * _local * 
   *   Q_engine = Q_base + K_P * (_target - )  [governor]
   */
  constructor({
    x = PROPELLER_X_DEFAULT,
    y = PROPELLER_Y_DEFAULT,
    inertia = PROPELLER_INERTIA,
    rpmNominal = PROPELLER_RPM_NOMINAL,
    kAero = K_AERO,
    governorKp = GOVERNOR_K_P,
    baseTorque = GOVERNOR_BASE_TORQUE
  } = {}) {
    // Position (stationary)
    this.x = x;
    this.y = y;

    // Physical properties
    this.inertia = inertia;
    this.kAero = kAero;

    // Governor parameters
    this.rpmNominal = rpmNominal;
    this.omegaTarget = this.rpmToRadS(rpmNominal);
    this.governorKp = governorKp;
    this.baseTorque = baseTorque;

    // State variables
    this.omega = this.omegaTarget;  // rad/s (start at target speed)
    this.rpm = rpmNominal;

    // Blade position tracking (for synchrophaser)
    this.bladeAngle = 0.0;  // radians (0 to 2), tracks blade #1 position

    // Cached values for visualization
    this.rhoLocal = RHO_SEA_LEVEL;
    this.qAero = 0.0;
    this.qEngine = baseTorque;
    this.qNet = 0.0;
    this.alpha = 0.0;
  }

  /**
   * Convert RPM to rad/s.
   */
  static rpmToRadS(rpm) {
    return rpm * 2.0 * Math.PI / 60.0;
  }

  /**
   * Convert rad/s to RPM.
   */
  static radSToRpm(omega) {
    return omega * 60.0 / (2.0 * Math.PI);
  }

  /**
   * Shorthand instance method for conversion
   */
  rpmToRadS(rpm) {
    return Propeller.rpmToRadS(rpm);
  }

  radSToRpm(omega) {
    return Propeller.radSToRpm(omega);
  }

  /**
   * Compute aerodynamic torque based on local density and angular velocity.
   *
   * Simplified model: Q_aero = k *  * 
   */
  computeAeroTorque(rhoLocal, omega) {
    return this.kAero * rhoLocal * Math.pow(omega, 2);
  }

  /**
   * Compute engine torque from simple proportional governor.
   *
   * Q_engine = Q_base + K_P * (_target - )
   */
  computeEngineTorque(omega) {
    const error = this.omegaTarget - omega;
    let qEngine = this.baseTorque + this.governorKp * error;

    // Limit to physically reasonable range (can't go negative)
    qEngine = Math.max(0.0, qEngine);

    return qEngine;
  }

  /**
   * Update propeller state for one time step.
   *
   * Samples local density, computes torques, and integrates equation of motion.
   */
  update(dt, simTime, densityField) {
    // Sample local atmospheric density at propeller position
    this.rhoLocal = densityField.getDensity(this.x, this.y, simTime);

    // Compute torques
    this.qAero = this.computeAeroTorque(this.rhoLocal, this.omega);
    this.qEngine = this.computeEngineTorque(this.omega);
    this.qNet = this.qEngine - this.qAero;

    // Compute angular acceleration:  = Q_net / I
    this.alpha = this.qNet / this.inertia;

    // Integrate: (t+dt) = (t) +  * dt
    this.omega += this.alpha * dt;

    // Prevent negative speeds (unrealistic)
    this.omega = Math.max(0.0, this.omega);

    // Update RPM for convenience
    this.rpm = this.radSToRpm(this.omega);

    // Update blade angle: (t+dt) = (t) +  * dt
    this.bladeAngle += this.omega * dt;
    // Wrap to [0, 2] to keep angle manageable
    this.bladeAngle = this.bladeAngle % (2.0 * Math.PI);
  }

  /**
   * Get current propeller state for logging/visualization.
   */
  getState() {
    return {
      x: this.x,
      y: this.y,
      rpm: this.rpm,
      omega: this.omega,
      rhoLocal: this.rhoLocal,
      qAero: this.qAero,
      qEngine: this.qEngine,
      qNet: this.qNet,
      alpha: this.alpha,
      rpmError: this.rpmNominal - this.rpm
    };
  }

  /**
   * Set target RPM for governor (used by synchrophaser).
   */
  setRpmTarget(rpmTarget) {
    this.rpmNominal = rpmTarget;
    this.omegaTarget = this.rpmToRadS(rpmTarget);
  }
}
