import React, { useState, useEffect, useRef } from 'react';
import './App.css';
import { DensityField } from './densityField';
import { Propeller } from './propeller';
import { Synchrophaser } from './synchrophaser';
import { PFDSynchrophaser } from './pfdSynchrophaser';
import {
  PROPELLER_X_DEFAULT,
  PROPELLER_LEFT_Y,
  PROPELLER_RIGHT_Y,
  PROPELLER_RPM_NOMINAL,
  DOMAIN_WIDTH,
  DOMAIN_HEIGHT
} from './parameters';

function App() {
  const [mode, setMode] = useState('off');
  const [isPaused, setIsPaused] = useState(false);
  const [stats, setStats] = useState({
    time: 0,
    rpmMain: PROPELLER_RPM_NOMINAL,
    rpmFollower: PROPELLER_RPM_NOMINAL,
    rpmError: 0,
    rollingAvg: 0,
    densityMain: 1.225,
    densityFollower: 1.225,
    phaseError: 0,
    correction: 0
  });

  const canvasRef = useRef(null);
  const simRef = useRef(null);
  const animationRef = useRef(null);

  // Initialize simulation
  useEffect(() => {
    // Create physics objects
    const densityField = new DensityField();
    const propMain = new Propeller({ x: PROPELLER_X_DEFAULT, y: PROPELLER_LEFT_Y });
    const propFollower = new Propeller({ x: PROPELLER_X_DEFAULT, y: PROPELLER_RIGHT_Y });
    const synchroBaseline = new Synchrophaser();
    const synchroAdvanced = new PFDSynchrophaser();

    // Rolling error tracking (15 second window)
    const errorHistory = [];
    const rollingWindowSeconds = 15.0;

    simRef.current = {
      densityField,
      propMain,
      propFollower,
      synchroBaseline,
      synchroAdvanced,
      errorHistory,
      rollingWindowSeconds,
      time: 0,
      lastUpdateTime: null
    };

    // Start animation loop
    const animate = () => {
      if (!isPaused && simRef.current) {
        updateSimulation();
        renderCanvas();
      }
      animationRef.current = requestAnimationFrame(animate);
    };
    animate();

    return () => {
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current);
      }
    };
  }, [isPaused]);

  // Update mode
  useEffect(() => {
    if (!simRef.current) return;

    const { synchroBaseline, synchroAdvanced, propFollower } = simRef.current;

    // Reset follower RPM setpoint
    propFollower.setRpmTarget(PROPELLER_RPM_NOMINAL);

    if (mode === 'off') {
      synchroBaseline.disable();
      synchroAdvanced.disable();
    } else if (mode === 'baseline') {
      synchroBaseline.enable();
      synchroAdvanced.disable();
    } else if (mode === 'advanced') {
      synchroBaseline.disable();
      synchroAdvanced.enable();
    }
  }, [mode]);

  const updateSimulation = () => {
    if (!simRef.current) return;

    const sim = simRef.current;
    const dt = 0.033;  // ~30 FPS
    sim.time += dt;

    // Update propellers
    sim.propMain.update(dt, sim.time, sim.densityField);
    sim.propFollower.update(dt, sim.time, sim.densityField);

    // Apply synchrophaser correction
    let correction = 0;
    if (mode === 'baseline') {
      correction = sim.synchroBaseline.update(
        sim.propMain.bladeAngle,
        sim.propFollower.bladeAngle,
        dt
      );
    } else if (mode === 'advanced') {
      correction = sim.synchroAdvanced.update(
        sim.propMain.bladeAngle,
        sim.propFollower.bladeAngle,
        sim.propMain.omega,
        sim.propFollower.omega,
        dt
      );
    }

    // Apply correction to follower
    if (correction !== 0) {
      sim.propFollower.setRpmTarget(PROPELLER_RPM_NOMINAL + correction);
    }

    // Update error history
    const rpmError = Math.abs(sim.propMain.rpm - sim.propFollower.rpm);
    sim.errorHistory.push({ time: sim.time, error: rpmError });

    // Remove old errors (beyond window)
    while (sim.errorHistory.length > 0 &&
           sim.time - sim.errorHistory[0].time > sim.rollingWindowSeconds) {
      sim.errorHistory.shift();
    }

    // Calculate rolling average
    let rollingAvg = 0;
    if (sim.errorHistory.length > 0) {
      const sum = sim.errorHistory.reduce((acc, entry) => acc + entry.error, 0);
      rollingAvg = sum / sim.errorHistory.length;
    }

    // Get active synchrophaser stats
    let phaseError = 0;
    if (mode === 'baseline') {
      const stats = sim.synchroBaseline.getStats();
      phaseError = stats.phaseErrorDeg;
    } else if (mode === 'advanced') {
      const stats = sim.synchroAdvanced.getStats();
      phaseError = stats.phaseErrorDeg;
    }

    // Update displayed stats
    setStats({
      time: sim.time,
      rpmMain: sim.propMain.rpm,
      rpmFollower: sim.propFollower.rpm,
      rpmError,
      rollingAvg,
      densityMain: sim.propMain.rhoLocal,
      densityFollower: sim.propFollower.rhoLocal,
      phaseError,
      correction
    });
  };

  const renderCanvas = () => {
    const canvas = canvasRef.current;
    if (!canvas || !simRef.current) return;

    const ctx = canvas.getContext('2d');
    const sim = simRef.current;

    // Set canvas size
    canvas.width = 800;
    canvas.height = 500;

    // Create grid for density field
    const gridX = 40;
    const gridY = 25;
    const xArray = Array.from({ length: gridX }, (_, i) => (i / (gridX - 1)) * DOMAIN_WIDTH);
    const yArray = Array.from({ length: gridY }, (_, i) => {
      return -DOMAIN_HEIGHT / 2 + (i / (gridY - 1)) * DOMAIN_HEIGHT;
    });

    // Get density grid
    const densityGrid = sim.densityField.getDensityGrid(xArray, yArray, sim.time);

    // Render density field
    const cellWidth = canvas.width / gridX;
    const cellHeight = canvas.height / gridY;

    for (let i = 0; i < gridY; i++) {
      for (let j = 0; j < gridX; j++) {
        const density = densityGrid[i][j];
        // Map density to color (blue = light, red = dense)
        const normalized = (density - 1.08) / (1.37 - 1.08);
        const r = Math.floor(255 * normalized);
        const g = Math.floor(150 * (1 - Math.abs(2 * normalized - 1)));
        const b = Math.floor(255 * (1 - normalized));

        ctx.fillStyle = `rgb(${r},${g},${b})`;
        ctx.fillRect(j * cellWidth, i * cellHeight, cellWidth, cellHeight);
      }
    }

    // Draw propellers
    const drawPropeller = (prop, color) => {
      // Convert propeller position to canvas coordinates
      const x = (prop.x / DOMAIN_WIDTH) * canvas.width;
      const y = ((prop.y + DOMAIN_HEIGHT / 2) / DOMAIN_HEIGHT) * canvas.height;

      // Draw propeller circle
      ctx.fillStyle = color;
      ctx.beginPath();
      ctx.arc(x, y, 15, 0, 2 * Math.PI);
      ctx.fill();

      // Draw blades
      ctx.strokeStyle = 'white';
      ctx.lineWidth = 3;
      for (let i = 0; i < 3; i++) {
        const angle = prop.bladeAngle + (i * 2 * Math.PI / 3);
        const dx = Math.cos(angle) * 12;
        const dy = Math.sin(angle) * 12;
        ctx.beginPath();
        ctx.moveTo(x, y);
        ctx.lineTo(x + dx, y + dy);
        ctx.stroke();
      }
    };

    drawPropeller(sim.propMain, '#22c55e');  // Green for main
    drawPropeller(sim.propFollower, mode === 'off' ? '#ef4444' : '#3b82f6');  // Red or blue for follower
  };

  return (
    <div className="App">
      <header>
        <h1> Synchrophaser Simulation</h1>
        <p>Three-Mode Comparison: OFF / BASELINE / ADVANCED</p>
      </header>

      <div className="description">
        <h3> What This Demonstrates</h3>
        <ul>
          <li><strong>OFF Mode:</strong> Propellers run independently - high RPM error and vibration</li>
          <li><strong>BASELINE Mode:</strong> PID synchrophaser - 69.5% error reduction</li>
          <li><strong>ADVANCED Mode:</strong> Phase-Frequency Detector - 73.0% error reduction</li>
        </ul>
        <p><strong>Try switching modes in real-time to see the difference!</strong></p>
      </div>

      <div className="controls">
        <div className="mode-buttons">
          <button
            className={`btn ${mode === 'off' ? 'btn-off' : 'btn-inactive'}`}
            onClick={() => setMode('off')}
          >
            OFF<br /><small>(No Sync)</small>
          </button>
          <button
            className={`btn ${mode === 'baseline' ? 'btn-baseline' : 'btn-inactive'}`}
            onClick={() => setMode('baseline')}
          >
            BASELINE<br /><small>(PID)</small>
          </button>
          <button
            className={`btn ${mode === 'advanced' ? 'btn-advanced' : 'btn-inactive'}`}
            onClick={() => setMode('advanced')}
          >
            ADVANCED<br /><small>(PFD)</small>
          </button>
        </div>

        <button
          className="btn btn-control"
          onClick={() => setIsPaused(!isPaused)}
        >
          {isPaused ? ' Resume' : ' Pause'}
        </button>
      </div>

      <div className="visualization">
        <div className="canvas-container">
          <canvas ref={canvasRef}></canvas>
        </div>

        <div className="info-panel">
          <div className="info-section">
            <h3>Mode</h3>
            <div className="stat">
              <span className="stat-label">Current:</span>
              <span className="stat-value">{mode.toUpperCase()}</span>
            </div>
            <div className="stat">
              <span className="stat-label">Time:</span>
              <span className="stat-value">{stats.time.toFixed(1)}s</span>
            </div>
          </div>

          <div className="info-section">
            <h3>Propeller RPM</h3>
            <div className="stat">
              <span className="stat-label">Main:</span>
              <span className="stat-value">{stats.rpmMain.toFixed(1)}</span>
            </div>
            <div className="stat">
              <span className="stat-label">Follower:</span>
              <span className="stat-value">{stats.rpmFollower.toFixed(1)}</span>
            </div>
            <div className="stat">
              <span className="stat-label">Error:</span>
              <span className="stat-value">{stats.rpmError.toFixed(1)}</span>
            </div>
            <div className="stat">
              <span className="stat-label">15s Avg:</span>
              <span className="stat-value">{stats.rollingAvg.toFixed(1)}</span>
            </div>
          </div>

          <div className="info-section">
            <h3>Density (kg/m)</h3>
            <div className="stat">
              <span className="stat-label">Main:</span>
              <span className="stat-value">{stats.densityMain.toFixed(3)}</span>
            </div>
            <div className="stat">
              <span className="stat-label">Follower:</span>
              <span className="stat-value">{stats.densityFollower.toFixed(3)}</span>
            </div>
          </div>

          {mode !== 'off' && (
            <div className="info-section">
              <h3>Synchrophaser</h3>
              <div className="stat">
                <span className="stat-label">Phase Error:</span>
                <span className="stat-value">{stats.phaseError.toFixed(1)}</span>
              </div>
              <div className="stat">
                <span className="stat-label">Correction:</span>
                <span className="stat-value">{stats.correction >= 0 ? '+' : ''}{stats.correction.toFixed(1)}</span>
              </div>
            </div>
          )}
        </div>
      </div>

      <footer>
        <p> Generated with Claude Code | <a href="https://github.com/naughtnowwhen/synchrophaser" target="_blank" rel="noopener noreferrer">View on GitHub</a></p>
      </footer>
    </div>
  );
}

export default App;
